@import "@/scss/settings.scss";






































































































































.page{
    z-index: 99999;
    position: relative;
    color : $color-white;
    max-width: 100vw;
    //background: #000;
    &__inner{
        z-index: 99999;
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100vh;
    }
    & .logo{
        opacity: 0;
        width: 400px;
        height: 400px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center center;
        margin: 0 0 30px 0;
    }
    & .title{
        opacity: 0;
        font-size: 5rem;
        color: $color-white;
        margin: 0 0 20px;
    }
}

.actions{
    opacity: 0;
}
.button.enter{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid white;
    background: transparent;
    color: $color-white;
    font-size: 18px;
    height: 50px;
    min-width: 150px;
    padding: 0 40px;
    line-height: 60px;
    border-radius: 25px;
    transition: background .5s ease;
    line-height: initial;
    margin: 0;
    margin: 30px auto;
    pointer-events: all;
    &:hover{
        background: rgba(255,255,255,0.4);
    }
}
.language{
    display: flex;
    justify-content: center;
}
.ll{
    font-size: 18px;
    color: white;
    background: transparent;
    margin: 10px;
    pointer-events: all;
    &.active{
        border-bottom: 2px solid white;
    }
}
.contact{
    font-size: 18px;
    color: white;
    position: fixed;
    top: 20px;
    right: 30px;
}
